

.backImg{
    z-index: -1;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-image: url('./pictures/backgroud/background3.jpg');
}

.loginFormWhole{
    display: flex;
    justify-content: center;
}

.logoImg{
    background-image: url("./pictures/logos/logo_only_icon/green-favicon/android-chrome-512x512.png");
    width: 35%;
    height: 35%;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    margin-top: 4rem;
    margin-left: 10rem;
}

.loginForm{
    display: block;
    background-color: rgb(54, 124, 43);
    padding: 4em;
    border-radius: 15px;
    box-shadow:1px 5px 5px 15px rgba(4, 4, 4, 0.368);
    margin-top: 17%;
}

.loginH2{
    text-align: center;
    color: rgb(255, 255, 255);
    position: relative;
    font-size: 24px;
    margin-top: -1em;
    margin-bottom: 1.5em;
    font-weight: 400;
    
}

.form-inner{
    position: relative;
    display: block;
    padding: 5px;
}

.loginForm-group{
    display: block;
    width: 300px;
    color: #666;
    background-color: rgb(240, 240, 240);
    padding: 20px;
    border-radius: 20px;
    box-shadow:2px 5px 5px 5px rgba(0, 0, 0, 0.183);
}
.loginLabel{
    display: block;
    color:  rgb(118, 118, 118);
    font-size: 16px;
    margin-bottom: 10px;
    transition: 0.4s;
}
.loginForm-group:focus-within .loginLabel{
    color: rgb(54, 124, 43);
}

.loginInput{
    display: block;
    width: 80%;
    padding: 10px 15px;
    background-color: white;
    border-radius: 8px;
    transition: 0.4s;
    border-color: rgb(195, 189, 189);
    margin-bottom: 15px;
}

.loginInput:focus{
    box-shadow: 0px 0px 3px rgba(0,0,0,0.2);
    background-color: rgb(247, 247, 247);
}
.loginButton{
    background-color: rgb(255, 222, 0);
    display: inline-block;
    padding: 10px 15px;
    border-radius: 8px;
    font-weight: bold;
    border: none;
    box-shadow: 1px 1px 1px 2px rgba(0, 0, 0, 0.101);
}
.loginButton:hover{
    background-color: rgba(207, 180, 6, 0.925);
}

.error{
    color: rgb(194, 16, 32);
}

.countrySelect{
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 15px;
    background-color: rgb(240, 240, 240);
    
}




